import React, {useState} from 'react';
import './css/App.css';
import profile from './images/profile.png';

function App() {
  const [inputTodo, setInputTodo] = useState('');
  const [todoList, setTodoList] = useState([]);
  
  const ENTER_KEY_CODE = 13;
  
  const inputTodoHandler = e => {
    setInputTodo(e.target.value);
  }
  
  const addTodoHandler = e => {
    if (e.keyCode === ENTER_KEY_CODE) {
      const tmp = todoList.slice();
      tmp.push({
        id: todoList.length,
        title: inputTodo,
        isCompleted: false
      });
      setTodoList(tmp);
    }
  }
  
  const toggleDoneHandler = id => {
    const tmp = todoList.slice();
    tmp[id].isCompleted = !tmp[id].isCompleted;
    setTodoList(tmp);
  }
  
  return (
    <div id="container">
      <div id="profile-area">
        <img src={profile} alt="profile-icon" />
        <div className="balloon">
          <p>やるよー</p>
        </div>
      </div>
      <div id="todo-area">
        <input type="text" className="input-todo" placeholder="Input Todo" value={inputTodo} onChange={inputTodoHandler} onKeyDown={addTodoHandler} />
        <ul className="todo-list">
          {todoList.map(todo => {
            return <li key={todo.id}><input type="checkbox" onChange={() => toggleDoneHandler(todo.id)} /><span className={`${todo.isCompleted && "done"}`}>{todo.title}</span></li>;
          })}
        </ul>
      </div>
    </div>
  );
}

export default App;
